@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;
  --mobile-xsmall-medium: "DM Sans";
  --font-inter: Inter;

  /* font sizes */
  --font-size-5xl: 24px;
  --mobile-heading-5-bold-size: 16px;
  --mobile-xsmall-medium-size: 13px;
  --mobile-base-regular-size: 14px;
  --font-size-xl: 20px;

  /* Colors */
  --light-mode-gray-10-f5f5f5: #f5f5f5;
  --light-mode-gray-20-eeeeee: #eee;
  --color-mediumslateblue: #365eec;
  --color-lavenderblush-300: #fdf1f4;
  --color-lightgray: #dccfcf;
  --color-gray-100: #828282;
  --light-mode-gray-60-757575: #757575;
  --light-mode-gray-95-1a1a1a: #1a1a1a;
  --light-mode-white-5-ffffff: #fff;
  --primary-color: #299d91;
  --color-gainsboro-100: #e6e6e6;
  --color-gainsboro: #e0e0e0;
  --color-palevioletred: #ff698d;
  --light-mode-blue-gray-20-9ab1bb: #9ab1bb;
  --color-black: #000;

  /* Gaps */
  --gap-xs: 12px;
  --gap-5xl: 24px;
  --gap-5xs: 8px;
  --gap-base: 16px;
  --gap-9xs: 4px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-3xs: 10px;
  --padding-6xs: 7px;
  --padding-base: 16px;
  --padding-xs: 12px;

  /* Border radiuses */
  --br-6xs: 7px;
  --br-9xs: 4px;
  --br-5xs: 8px;
}
